$font-stack: -apple-system, BlinkMacSystemFont,Roboto,'Open Sans','Helvetica Neue',sans-serif;

@import '../../node_modules/slick-carousel/slick/slick.scss';

* {
  box-sizing: border-box;
}
ul{
  margin: 0;
  padding: 0;
}
body {
  font-family: $font-stack;
  margin: 0;
  padding: 0;
  max-width: 100%;
  .btn {
    width: 200px;
    height: 50px;
    border-radius: 100px;
    font-family:  $font-stack;
    font-size: 13px;
    font-weight: bold;
    font-style: normal;
    line-height: 1.54;
    letter-spacing: 0.5px;
    text-align: center;
    text-transform: uppercase;
    &-red{
      background-color: #ff0d35;
      color: #ffffff;
    }
  }
.container {
    	max-width: 1530px;
    	width: 100%;
    	margin: auto;
    }
header{
    position: relative;
    max-width: 100%;
    margin: auto;

    .header-top {
      position: absolute;
      max-width: 1530px;
      display: flex;
      width: 100%;
      align-items: center;
      justify-content: space-around;
    }

}
.section-main{
    max-width: 100%;
    background-color: #ffffff;
    margin: -120px auto 0; 
    position: relative;
    padding: 60px 100px 140px;

    .title {
      max-width: 600px;
      font-family: Teko;
      font-size: 56px;
      font-weight: 600;
      font-style: normal;
      line-height: 1.07;
      color: #000000;
    }
 
    .dots {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 30px;
        margin: auto;
        padding: 0;
        text-align: center;
        width: 100%;
      }

    .dots li {
        display: inline-block;
        margin: 0 5px;  
      }

    .dots button {
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background: #ffffff;
        display: block;
        overflow: hidden;
        text-indent: 10000px;
        border: none;
        outline-color: transparent;
    }

    .dots .slick-active button {
        background: #ff0d35;
      }
    }   
  }
    ul {
        list-style: none;
        margin: 0;
        padding: 0;
      }
    
      .item-icon {
        display: inline-block;
        margin: 0 10px;
      }
    